.remainingTime {
    font-size: 15em;
}

.btnStart {
    margin-right: 5px;
}

.btnReset {
    margin-left: 5px;
}

.btnInput {
    border: 1px solid #eee;
    height: 36px;
    margin-right: 5px;
    transform: translateY(3px);
}

.timer {
    font-size: 5em;
    color: red;
}
@media screen and (max-width: 768px){
    .remainingTime{
        font-size: 5em;
    }
    .timer {
        font-size: 2em;
    }
}

